import * as firebase from 'firebase'
import URL from '../URL'

class User {
  constructor (id) {
    this.id = id
  }
}

export default {
  state: {
    user: null
  },
  mutations: {
    setUser (state, payload) {
      state.user = payload
    }
  },
  actions: {
    async registerUser ({commit}, payload) {
      commit('clearSnackbar')
      //commit('setUser', new User(user.uid))
      return fetch(`${URL}/user`,
        {
          mode: 'cors',
          headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json',
            'Access-Control-Allow-Origin': '*',
            'Access-Control-Allow-Headers': '*',
          },
          method: 'POST',
          body: JSON.stringify(payload)
        })
        .then(response => response.json())
        .then(
          json => {
            console.log(json)
            if (json.status === -1) {
              console.log(json.message)
              commit('setSnackbarMsg', json.message)
              commit('setSnackbarType', 'error')
              return false
            } else {
              commit('togleRegisterDialog')
              commit('setSnackbarMsg', 'Успешная регистрация')
              commit('setSnackbarType', 'success')
              return true
            }
          }
        )
        .catch(
          error => {
            console.error(error)
            commit('setSnackbarMsg', 'Ошибка регистрации')
            commit('setSnackbarType', 'error')
            return false
          }
        )
    },
    async loginUser ({commit}, payload) {
      commit('clearSnackbar')

      return fetch(`${URL}/user/auth`,
        {
          mode: 'cors',
          headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json',
            'Access-Control-Allow-Origin': '*',
            'Access-Control-Allow-Headers': '*',
          },
          method: 'POST',
          body: JSON.stringify(payload)
        })
        .then(response => response.json())
        .then(
          json => {
            console.log(json)
            if (json.status === 1) {
              commit('togleLoginDialog')
              //commit('setUser', new User(user.uid))
              commit('setSnackbarMsg', 'Успешная авторизация')
              commit('setSnackbarType', 'success')
              return true
            } else {
              commit('setSnackbarMsg', json.message)
              commit('setSnackbarType', 'error')
              return false
            }
          }
        )
        .catch(
          error => {
            console.error(error)
            commit('setSnackbarMsg', 'Ошибка авторизации')
            commit('setSnackbarType', 'error')
            return false
          }
        )
    },
    async resetPassword ({commit}, payload) {
      commit('clearSnackbar')

      return fetch(`${URL}/user/recovery`,
        {
          headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json'
          },
          method: 'POST',
          body: JSON.stringify(payload)
        })
        .then(response => response.json())
        .then(
          json => {
            console.log(json)
            if (json.status === -1) {
              commit('setSnackbarMsg', json.message)
              commit('setSnackbarType', 'error')
              return false
            } else {
              commit('togleResetPasswordDialog')
              commit('setSnackbarMsg', 'Пароль выслан на e-mail')
              commit('setSnackbarType', 'success')
              return true
            }
          }
        )
        .catch(
          error => {
            console.error(error)
            commit('setSnackbarMsg', 'Ошибка восстановления пароля')
            commit('setSnackbarType', 'error')
            return false
          }
        )
    },
    autoLoginUser ({commit}, payload) {
      commit('setUser', new User(payload.uid))
    },
    logoutUser ({commit}) {
      firebase.auth().signOut()
        .then(() => {
          commit('setUser', null)
        })
        .catch(err => {
          console.error(err)
          commit('setSnackbarMsg', err.message)
          commit('setSnackbarType', 'error')
        })
    }
  },
  getters: {
    user (state) {
      return state.user
    },
    isUserLoggedIn (state) {
      return state.user !== null
    }
  }
}



